.logo{
	&-icon{
		@include desktop {
			width: 50px;
			height: 65px;
		}
	}

	padding: $link-area-increase;
	margin: -$link-area-increase;

}

.footer .logo-icon {
	display: none;

	@include tablet {
		display: block;
		filter: grayscale(1);
		transition-duration: $transition-duration;
	}

	@include desktop {
		width: 34px;
	}
}

.footer .logo:hover .logo-icon {
	filter: brightness(1%);
}
