// stylelint-disable scss/dollar-variable-empty-line-before

// COLORS

$font-color: #333;
$heading-color: #2a2a2a;
$accent-color: #70e83c;
$contrast-color: rgba(251, 193, 5, 0.78);
$layout-highlight: #fafafa;
$not-focused: #515154;
$hover: #1d1d1f;
$light-not-focused: rgba(255, 255, 255, .75);
$light-hover: rgba(255, 255, 255, 1.0);

// layout

$mobile-main-width: 87.5%;
$tablet-main-width: 92.2%;
$desktop-main-width: 84.8%;

$mobile-main-padding: 6.25%;
$tablet-main-padding: 3.9%;
$desktop-main-padding: 7.6%;

$desktop-inner-padding: 2.77%;

//

$link-area-increase: 10px;

//

$transition-duration: 0.3s;
