.hero {
	&__heading {
		font-size: 30px;
		margin-block-end: 0em;
		margin-block-start: 0em;
		width: $mobile-main-width;
		color: #fff;

		@include tablet {
			width: 100%;
			font-size: 35px;
		}

		@include desktop {
			position: absolute;
			left: 13.19%;
			top: 20vh;
			max-width: 35%;
			font-size: 38px;
			color: $heading-color;
		}
	}

	&__subheading {
		margin-top: 20px;
		width: $mobile-main-width;
		font-family: "Open Sans Condensed", sans-serif;
		font-weight: 300;
		font-size: 14px;
		text-transform: uppercase;
		color: #fff;

		@include tablet {
			width: 100%;
			margin-top: 35px;
			font-size: 16px;
		}

		@include desktop {
			position: absolute;
			left: 13.19%;
			max-width: 35%;
			top: 38vh;
			font-size: 20px;
			font-weight: 600;
			color: $heading-color;
		}
	}

	&__statement {
		position: absolute;
		bottom: 20px;
		padding: 14px;
		width: 250px;
		font-family: "Open Sans Condensed", sans-serif;
		font-weight: 600;
		font-size: 23px;
		font-style: italic;
		text-align: center;
		text-transform: uppercase;
		background-color: $contrast-color;
		color: #fff;
		border-radius: 90px 0 90px 0;

		@include tablet {
			bottom: 40px;
			align-self: flex-start;
		}

		@include desktop {
			left: 13.19%;
			top: 60vh;
			bottom: auto;
			background-color: lighten($contrast-color, 30);
			color: $contrast-color;
		}
	}

	position: relative;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: center;
	margin-right: -$mobile-main-padding;
	margin-left: -$mobile-main-padding;
	padding-top: $desktop-inner-padding*2;
	padding-right: $desktop-inner-padding;
	padding-left: $desktop-inner-padding;
	padding-bottom: 0;
	width: 100vw;
	height: 400px;
	background-color: #a5a178;
	background-image: url("/images/hero-600x400.jpg");
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	text-align: center;

	@include retina {
		background-image: url("/images/hero-1200x800.jpg");
	}

	@include tablet {
		width: 100%;
		text-align: left;
		padding: 60px $tablet-main-padding*3 90px $tablet-main-padding*3;
		margin-right: 0;
		margin-left: 0;
	}

	@include desktop {
		position: static;
		width: 50vw;
		height: 100vh;
		background-color: #a5a178;
		background-image: url("/images/hero-d-960x700.jpg");
		background-position: top left;
		background-size: auto 100%;
		margin-left: auto;

		@include retina {
			background-image: url("/images/hero-d-1920x1400.jpg");
		}
	}
}
