.footer {
	&__text{
		margin-right: 15px;
	}

	&__icon {
		margin-top: -4px;
		fill: $not-focused;
	}

	&__icon-link{
		display: flex;
		padding: $link-area-increase;
		margin: -$link-area-increase;
		text-decoration: none;
		color: $not-focused;
		transition-duration: $transition-duration;

		&:hover .footer__icon {
			fill: $hover;
		}

		&:hover .footer__text {
			color: $hover;
			text-decoration:underline
		}
	}

	&__tel {
		position: absolute;
		bottom: -40px;
		text-decoration: none;

		&:hover {
			text-decoration:underline
		}
	}

	margin-bottom: 60px;
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
	border-top: 1px solid rgba(0, 0, 0, 0.15);
	border-bottom: 1px solid rgba(0, 0, 0, 0.15);
	padding-top: 24px;
	padding-bottom: 24px;
	padding-left: $mobile-main-padding;
	padding-right: $mobile-main-padding;
	width: $mobile-main-width;
	color: $not-focused;

	@include tablet {
		border: none;
		justify-content: space-between;
		align-items: center;
		width: $tablet-main-width;
		padding-left: 0;
		padding-right: 0;
	}

	@include desktop {
		width: $desktop-main-width;
	}
}
