.button-link {
	&__whatsapp-icon {
		margin-right: 15px;
		fill: $light-not-focused;
	}

	&:hover .button-link__whatsapp-icon {
		fill: $light-hover;
	}

	&:visited {
		color: $light-not-focused;
	}

	width: fit-content;
	max-width: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 10px 24px;
	font-weight: 300;
	font-size: 14px;
	text-align: center;
	text-decoration: none;
	text-transform: uppercase;
	color: $light-not-focused;
	background-color: $accent-color;
	border-radius: 30px;
	transition-duration: $transition-duration;

	&:hover {
		color: $light-hover;
	}

	@include tablet {
		padding: 14px 28px;
		font-size: 20px;
		border-radius: 34px;
	}

	@include desktop {
		width: 30%;
	}
}
