.header {
	&__nav {
		@include tablet {
			margin-left: auto;
		}
	}

	&--sticky {
		position: fixed;
		top: 0;
		width: 100%
	  }

	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 4px $mobile-main-padding;
	width: 100%;
	background-color: #fff;
	z-index: 1;

	@include tablet {
		padding-top: 20px;
		padding-bottom: 0;
		padding-right: $tablet-main-padding;
		padding-left: $tablet-main-padding;
	}

	@include desktop {
		width: $desktop-main-width;
		position: absolute;
		top: 0;
		padding-top: 2vh;
		background-color: transparent;
	}
}

.header--sticky + .page-main__hero {
	padding-top: 56px;

	@include tablet {
		padding-top: 70px;
	}

	@include desktop {
		padding-top: 82px;
	}
  }
