*,
*::before,
*::after {
	box-sizing: inherit;
}

html {
	box-sizing: border-box;
}



a {
	color: $not-focused;

	&:hover {
		color: $hover;
	}

	&:visited {
		color: $not-focused;
	}
}

blockquote {
	background: #6AD3A8;
    padding: 15px;
	margin-bottom: 20px;
	box-shadow: 0 1.5px 4px rgba(0, 0, 0, 0.24), 0 1.5px 6px rgba(0, 0, 0, 0.12);
}

