.reviews {
	&__heading {
		@include desktop{
			font-size: 40px;
		}
	}

	&__note {
		margin-top: -10px;
	}

	&__note-link {
		margin-left: 10px;
	}

	padding-top: 20px;
	width: $mobile-main-width;

	@include tablet {
		width: $tablet-main-width;
	}

	@include desktop {
		width: $desktop-main-width;
	}
}
