.advantage {
	&__heading {
		font-size: 18px;
		text-transform: uppercase;

		@include tablet {
			font-size: 25px;
		}

		@include desktop {
			font-size: 30px;
		}
	}
	&__text {
		@include tablet {
			padding-top: 30px;
		}
	}

	&__video-link{
		margin-top: 10px;
	}

	&__link-wrapper {
		margin-top: 15px;
		display: flex;
		border-top: 1px solid rgba(0, 0, 0, 0.15);
	}


	&__arrow-right{
		fill: $not-focused;
	}

	&__link {
		display: flex;
		align-items: center;
		margin-top: 20px;
		margin-bottom: 20px;
		text-decoration: none;
		transition-duration: $transition-duration;

		&:hover .advantage__arrow-right{
			fill: $hover;
		}
	}

	&__link-text {
		margin-right: 15px;
		font-weight: 700;
		font-size: 14px;
		text-transform: uppercase;

		@include tablet {
			font-size: 16px;
		}

		@include desktop {
			font-size: 18px;
		}
	}

	display: flex;
	flex-direction: column;
	justify-content: space-between;
	margin-bottom: 30px;
	padding: 20px $mobile-main-padding 0 $mobile-main-padding;
	background-color: $layout-highlight;

	@include tablet {
		width: 50% - $desktop-inner-padding / 2;
		padding: $tablet-main-padding $tablet-main-padding 0 $tablet-main-padding;
	}

	@include desktop {
		width: 50% - $desktop-inner-padding;
	}
}
