.language-switch {
	&__wrapper{
		&:hover {
			box-shadow: 0px 0 0 4px lighten($accent-color, 20);
			border: none;

			@include desktop {
				box-shadow: none;
				border: 2px solid $light-hover;
			}
		}

		&:active {
			box-shadow: 0px 0 0 8px lighten($accent-color, 30);
			border: none;

			@include desktop {
				box-shadow: 0px 0 0 8px $light-not-focused;
				border: none;
			}
		}

		@include tablet {
		display: flex;
		justify-content: center;
		align-items: center;
		margin-top: -7px;
		border: 2px solid $accent-color;
		border-radius: 50%;
		width: 28px;
		height: 28px;
		transition-duration: $transition-duration;
		}

		@include desktop {
			&:hover .language-switch__link{
				color: $light-hover;
			}

			border: 2px solid $light-not-focused;
			width: 45px;
			height: 45px;
		}
	}

	&__link {
		font-weight: 600;
		font-size: 14px;
		color: #326f44;

		@include desktop {
			color: $light-not-focused;
		}
	}

	display: none;

	@include tablet {
		display: flex;
		padding: $link-area-increase;
		margin: -$link-area-increase;
		text-decoration: none;
	}

}

.footer .language-switch {
	&__wrapper {
	border: 2px solid $not-focused;

	@include tablet {
		width: 28px;
		height: 28px;

		.language-switch__link{
			color: $not-focused;
		}

		&:hover {
			.language-switch__link{
				color: $hover;
			}

			box-shadow: none;
			border: 2px solid $hover;
		  }

		&:active {
			box-shadow: none;
		}
	}
}

	&__link {
		color: $not-focused;
	}

}
